nna: rgba(231, 111, 81, 0.9);\n$semi-trans_persian-green: rgba(92, 34, 0, 0.3);\n$semi-trans_main-light: rgba(245, 245, 245, 0.5);\n\n\n\n\n// ------- TYPOGRAPHY -------//\n:root {\n    \n    font: {\n        family: TerraIgnota;\n        size: 18px;\n    }\n}\n\n.wrapper {\n    background-image: url('../media/icons/oldtime_map_background.jpg');\n    background-size: cover;\n    overflow: hidden;\n}\n\nhtml, body {\n    margin: 0;\n    min-height: 100%;\n}\n\nh1, h2, h3, h4 {\n    font-family: TerraIgnota;\n    color: $main-dark;\n}\n\nh1 {\n    font: {\n        size: 4rem;\n        family: 'Fredericka the Great';\n    }\n    margin: 0;\n    padding-left: 1rem;\n    padding-bottom: 0.5rem;\n}\n\nh2 {\n    font-size: 3.5rem;\n    margin: 0;\n}\n\n* {\n    box-sizing: border-box;\n    border-radius: 6px;\n}\n\n// ----- PRELIMINARY PRINT FORMATTING ----- //\n@media print {\n    .section-container {\n                border: fuchsia;\n                color: fuchsia;\n            }\n\n    header, footer, form {\n        display: none;\n    }\n}\n\n@page {\n    margin: 2cm;\n}\n\n","@import 'base';\n\n.wrapper {\n    padding: 0;\n    display: grid;\n    grid-gap: 3px;\n    grid-template-columns: repeat(12, 1fr);\n    grid-template-rows: 80px auto auto 140px;\n    grid-template-areas:\n    \"hd hd hd hd hd hd hd hd hd hd hd hd\"\n    \"ef ef ef ef ef ef ef ef ef ef ef ef\"\n    \"if if if if if if cd cd cd cd cd cd\"\n    \"if if if if if if cd cd cd cd cd cd\"\n    \"cf cf cf cf wr wr wr wr tl tl tl tl\"\n    \"cf cf cf cf wr wr wr wr pl pl pl pl\"\n    \"f f f f f f f f f f f f\";\n}\n\n.section-container_img-facts {\n    grid-area: if;\n    background-color: $trans-background_maize-crayola;\n\n}\n\n.section-container_entry-form {\n    grid-area: ef;\n    background-color: $trans-background_green;\n\n}\n\n.section-container_weather {\n    grid-area: wr;\n    background-color: $trans-background_green;\n\n    .current-weather {\n        background-color: $trans-background_aqua;\n    }\n\n\n    #forecast {\n        display: flex;\n        padding: 1em;\n        border-radius: 4px;\n        background-color: rgba(255, 102, 0, 0.1);\n        justify-content: space-between;\n        gap: 0.5em;\n        text-align: center;\n        overflow: scroll;\n        box-shadow: 1px 1px 6px 1px rgba(128, 128, 128, 0.4);\n\n        li {\n            border-radius: 14px;\n            background-color: turquoise;\n            min-height: 10vw;\n            box-shadow: 1px 1px 12px 1px rgba(128, 128, 128, 0.4);\n        }\n    }\n\n    #forecast::-webkit-scrollbar {\n        display: none;\n    }\n}\n\n.section-container_countdown {\n    grid-area: cd;\n    background-image: url('../media/icons/steampunk-clock_image.png');\n    background-size: cover;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n\n    .countdown {\n        z-index: 1;\n        background-color: transparent;\n\n        ul {\n            padding: 0;\n        }\n    }\n\n    li {\n        background-color: rgba(253, 246, 226, 0.75);\n    }\n    \n    .countdown-digits {\n        font: {\n            family: 'Fredericka the Great';\n            size: 3rem;\n        }\n        color: $main-light;\n    }\n\n    #countdown-background {\n        background-color: rgba(150, 56, 28, 0.8);\n    }\n\n    #remaining-time {\n        ul {\n            padding:0;\n            margin: 0;\n        }\n    }\n\n}\n\n.section-container_facts {\n    grid-area: cf;\n    background-color: $trans-background_test-color;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n}\n\n.section-container_pack-list {\n    grid-area: pl;\n    background-color: $trans-background_aqua;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.section-container_trip-length {\n    grid-area: tl;\n    background-color: $trans-background_green;\n}\n\n.section-container_header {\n    grid-area: hd;\n    background-color: $main-light;\n    h1 {\n        color: $main-dark;\n    }\n}\n\n\n.section-container_footer {\n    grid-area: f;\n    background-color: $trans-background_green;\n\n}\n\nbody {\n    font: {\n        family: 'TerraIgnota';\n        size: 18px;\n    }\n    background-color: $main-light;\n    color: #444;\n    width: 100vw;\n    margin: 0;\n}\n\nbutton {\n    padding: 1em;\n    width: 180px;\n    border: none;\n    border-radius: 4px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);\n    background-color: $border-color;\n}\n\ndiv {\n    padding: 1em;\n\n    // background-color: $main-light;\n}\n\nbutton:hover {\n    color: $main-dark;\n    background-color: $main-light;\n    transition: ease-in 0.2s;\n}\n\nform {\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);\n    max-width: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 0.5rem;\n    list-style-type: none;\n}\n\nimg {\n    width: 100%;\n    height: auto;\n}\n\nli {\n    background-color: $main-light;\n    margin: 0;\n    list-style-type: none;\n    cursor: pointer;\n    border-top: 1px solid #e5e5e5;\n    font-size: 1.5rem;\n    padding: 1rem 2rem;\n    border-radius: 4px;\n}\n\nsection {\n    background-color: rgba(245, 245, 245, 0.6);\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);\n\n}\n\nul {\n    display: flex;\n    flex-direction: column;\n    list-style-type: none;\n    align-content: center;\n}\n\n\n\n.close {\n    float: right;\n    padding: 0.4em;\n    border: solid rgba(128, 128, 128, 0.4);\n}\n\n#country-photo {\n    box-shadow: 1px 1px 19px 5px rgba(88, 37, 37, 0.9);\n}\n\n\n\n.hidden {\n    display: none;\n}\n\n.input {\n    border: none;\n    color: #444;\n    font-size: 2rem;\n    padding: 1rem;\n    display: block;\n    margin: auto;\n    width: 60%;\n}\n\n.input::placeholder {\n    color: rgba(143, 94, 31, 0.4);\n}\n\n.input:focus {\n    outline: 3px solid $semi-trans_persian-green;\n}\n\n\n#flag {\n    width: 50%;\n    height: auto;\n}\n\n\n\n#pack-list-btn {\n    background-color: $semi-trans_burnt-sienna;\n    font: {\n        size: 1.5em;\n        family: 'Shadows Into Light Two', sans-serif;\n        weight: bold;\n    }\n\n    min-width: 60%;\n\n    // :hover {\n        \n    // }\n}\n\n#pack-list-btn:hover {\n    color: $main-light;\n        background-color: $maize-crayola;\n}\n\n.modal {\n    display: none; /* Hidden by default */\n    position: fixed; \n    z-index: 1; \n    padding-top: 5vh; \n    left: 0;\n    top: 0;\n    width: 100vw; \n    height: 100vh; \n    margin: 0;\n    overflow: auto; \n    background-color: rgb(0,0,0); \n    background-color: rgba(0,0,0,0.6); \n  }\n  \n  .modal-content {\n    margin: auto;\n    background-image: url('../media/icons/vintage-notebook_CROPPED.jpg');\n    background-size: cover;\n    background-repeat: no-repeat;\n    padding: 0.25em;\n    border: 1px solid #888;\n    overflow: hidden;\n    width: 80%;\n    height: 100%;\n    justify-content: center;\n    align-items: center;\n    overflow: scroll;\n\n    h3 {\n        margin-left: 2em;\n        font-size: 3em;\n        padding: 0;\n        margin:  0 0 0 1em;\n    }\n   \n\n    /* The Close Button */\n    .close {\n        position: relative;\n        color: $main-dark;\n        font-size: 48px;\n        font-weight: bold;\n        padding:0 0.175em;\n        border-color: $main-dark;\n    }\n\n        .close:hover,\n        .close:focus {\n            background-color: $trans-background_persian-green;\n            color: $main-light;\n            border-color: $semi-trans_persian-green;\n            text-decoration: none;\n            cursor: pointer;\n        }\n        #pack-list-form {\n            box-shadow: none;\n\n            .input#input {\n                margin-left: 1em;\n                max-width: 45%;\n                background-color: $semi-trans_main-light;\n            }\n            \n            .input#input:focus {\n                outline-color: $maize-crayola;\n    